$font-stack: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", sans-serif;

@import "fonts";
@import "smart-grid";
@import "variables";

body {
  font-size: 18px;
  font-family: "SF Pro Display", sans-serif;

  &.tpl {
    background: url("../../img/layouts/find-work-3.png") no-repeat center 0;
    height: 5000px;
    background-size: 1560px;
    position: relative;
    opacity: .6;
    z-index: -20;
  }

  &.main {
    position: relative;

  }

  &.main:before {
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    background: url("../img/bg_main.png") no-repeat top right;
    width: 100%;
    height: 100%;
    background-size: contain;
    z-index: -10;
    @include md-block() {
      display: none;
    }
    @include lg-block() {
      display: none;
    }
    @include sm-block() {
      display: none;
    }
    @include xs-block() {
      display: none;
    }
  }
}

header, section, footer {
  > div {
    @include wrapper();

    > div {
      @include xs-block() {
        margin: 0 !important;
      }
    }
  }
}

//
.bg-container {
  position: fixed;
  top: 20px;
  right: 20px;

  label {
    width: 30px;
    height: 30px;
    background: black;
    position: absolute;
  }

  input {
    display: none;
  }
}

//
/*Header*/
.header {
  @include row-flex();
  @include col();
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 19px 0 16px;
  @include md-block() {
    display: none;
  }

  &-elements {
    display: flex;
    align-items: center;
  }

  &-logo {
    @include md-block() {
      display: inline-flex;
      order: -1;
    }

    a {
      font-size: 30px;
      font-weight: bold;
    }

    b {
      color: #3993FB;
    }

    span {
      color: #FEB627;
    }
  }

  &-links {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    margin-left: -218px;
    @include md-block() {
      flex-wrap: wrap;
      flex-direction: column;
      display: flex;
      width: 100%;
      text-align: left;
      align-items: flex-start;
      order: 1;
      padding-top: 30px;
      padding-left: 0;
      margin: 0;
    }

  }

  &-link {
    display: inline-flex;
    padding-right: 36px;
    @include md-block() {
      padding-top: 20px;
    }


    &:hover {
      a {
        color: #F39E31;
        transition: ease-in-out .3s;
      }
    }

    a {
      font-size: 16px;
      color: #1A3E74;
      @include md-block() {
        font-size: 26px;
      }
    }
  }

  &-info {
    &-items {
      display: flex;
      align-items: center;
      @include md-block() {
        padding-top: 40px;
      }
    }

    &-item {
      padding-left: 10px;

      &.profile {
        @include md-block() {
          order: -1;
        }
      }

      &-profile {
        display: flex;
        justify-content: center;
        width: 50px;
        height: 50px;
        border: 2px solid #FEB627;
        border-radius: 10px;

        img {
          border-radius: 50%;
        }

        @include md-block() {
          border-radius: 50%;
          width: 75px;
          height: 75px;
          z-index: -1;
          img {
            border-radius: 50%;
          }
        }
      }
    }
  }

  &.border-bottom {
    border-bottom: 1px solid #DCE6F2;
  }
}

/*Mobile menu*/

.mobile-menu {
  &-container {
    @include row-flex();
    @include col();
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
  }

  &-logo {
    a {
      font-size: 30px;
      font-weight: bold;
    }

    b {
      color: #3993FB;
    }

    span {
      color: #FEB627;
    }
  }

  input {
    display: none;
  }

  input + label {
    display: flex;
    width: 30px;
    height: 30px;
    background: url("../img/burger.png") no-repeat center;
    background-size: cover;
    cursor: pointer;
    z-index: 20;
  }

  display: none;
  @include md-block() {
    display: block;
  }
}

/* Add Class to .header*/
.mobile {
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  background: white;
  z-index: 10;
  align-items: flex-start;
  padding-top: 15px;
}

