      }
    }

    h4 {
      padding-top: 20px;
    }
  }

  &-search {
    height: 70px;
    padding-top: 65px;
    @include xs-block() {
      height: auto;
    }

    form {
      height: 100%;
      width: 670px;
      position: relative;
      @include xs-block() {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }

    input {
      height: 100%;
      width: 100%;
      border: 1px solid #DCE6F2;
      border-radius: 35px;
      padding-left: 24px;
      @include xs-block() {
        height: 70px;
      }

      &::placeholder {
        color: #7C9CBB;
        font-size: 18px;
      }
    }

    button {
      height: 100%;
      width: 194px;
      background: #FEB627;
      border: none;
      box-shadow: 0 10px 15px #F39E3154;
      border-radius: 50px;
      cursor: pointer;
      position: absolute;
      right: 0;
      @include xs-block() {
        position: static;
        height: 70px;
        margin-top: 20px;
      }

      span {
        font-size: 18px;
        font-weight: bold;
        color: #FFFFFF;
      }
    }
  }
}

