  }

  &-left {
    width: calc(25% - 25px);
    display: flex;
    flex-direction: column;
    align-items: center;

    &-performer {
      width: 100%;
      border: 1px dashed #feb627;
      background-color: #fffae4;
      border-radius: 15px;

      a {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 30px 25px;

      }

      b {
        color: #60594f;
        font-size: 19px;
        font-weight: 700;
        line-height: 26px;
        padding-left: 18px;
      }
    }

    &-categories {
      width: 100%;
      border-radius: 15px;
      border: 1px solid #dce6f2;
      background-color: #f8fbff;
      margin-top: 20px;

      &-container {
        padding-top: 20px;
        padding-left: 20px;
      }

      &-main {
        display: flex;
        border-bottom: 1px solid #dce6f2;
        align-items: center;
        padding-bottom: 20px;
      }

      &-items {
        padding-top: 20px;
      }

      &-item {
        padding-bottom: 17px;
      }

    }

    &-banner {
      background: url("../img/sections/findWork1/banner.png") no-repeat top;
      background-size: 100%;
      display: flex;
      flex-direction: column;
      text-align: left;
      margin-top: 30px;
      padding: 30px 30px 50px;

      p {
        color: #ffffff;
        font-size: 16px;
        font-weight: 400;
        letter-spacing: 0.8px;
        line-height: 36px;
        text-transform: uppercase;
        padding-top: 10px;
      }

      b {
        color: #ffffff;
        font-size: 26px;
        font-weight: 700;
        line-height: 36px;
        padding-top: 40px;
      }

      &-btn {
        display: flex;
        justify-content: center;
        margin-top: 45px;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 165px;
        height: 51px;
        border-radius: 10px;
        background-color: #ffd21d;

        span {
          color: #1a3e74;
          font-size: 16px;
          font-weight: 700;
        }
      }
    }
  }

  &-right {
    width: calc(75% - 5px);
    display: flex;
    flex-direction: column;

    &-form {
      display: flex;
      flex-direction: column;
      border-radius: 15px;
      border: 1px solid #dce6f2;
      background-color: #f8fbff;
      padding: 20px;

      h3 {
        color: #1a3e74;
        font-size: 26px;
        font-weight: 800;
      }

      input[type='text'], input[type='search'] {
        height: 50px;
        border-radius: 10px;
        border: 1px solid #dce6f2;
        background-color: #ffffff;
        padding-left: 18px;
        color: #7c9cbb;
        font-size: 16px;
        font-weight: 400;
        &:focus {
          border: 1px solid #2ab9f9;
          outline: none;
        }
      }

      &-search {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-top: 20px;

        input {
          width: 643px;
          height: 50px;
        }

        button {
          width: 116px;
          height: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
          box-shadow: 0 10px 15px rgba(243, 158, 49, 0.33);
          border-radius: 10px;
          background-color: #feb627;
          border: none;

          span {
            color: #ffffff;
            font-size: 18px;
            font-weight: 700;
          }
        }

      }

      &-inputs {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        padding-top: 10px;
        &-address {
          width: 360px;
        }
        &-radius {
          width: 164px;
        }
        &-cost {
          width: 239px;
        }

        > div {
          display: inline-flex;
          flex-direction: column;
          text-align: left;
          padding-right: 17px;
          &:last-of-type {
            padding-right: 0;
          }

          span {
            color: #1a3e74;
            font-size: 16px;
            font-weight: 500;
            padding-bottom: 10px;
          }
        }
      }

      &-checkboxes {
        @include size(7);
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        text-align: left;
        flex-wrap: wrap;
        padding-top: 20px;
      }
      &-checkbox {
        width: 50%;
        padding-bottom: 10px;
      }
    }

  }
}

/*Footer*/

footer {
  &.main {
    background-color: #F8FBFF;
  }
}

.footer {
  &.main {
    border: none;
  }

  &.border-top {
    border-top: 1px solid #DCE6F2;
    padding-top: 70px;
  }

  @include row-flex();
  @include col();
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 85px;
  @include xs-block() {
    padding-top: 40px;
  }

  h2 {
    font-weight: bold;
    font-size: 50px;

    a {
      color: #DCE6F2;
    }
  }

  p {
    padding-top: 35px;
    font-size: 18px;
    color: #BCCBD9;
  }

  &-links {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    padding-top: 42px;
    @include xs-block() {
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }

  &-link {
    padding: 0 21px;
    @include xs-block() {
      width: 100%;
      padding-bottom: 15px;
    }

    a {
      font-size: 18px;
      font-weight: normal;
      color: #7C9CBB;
      @include xs-block() {
        font-size: 22px;
      }
    }

    &:hover {
      a {
        text-decoration: underline;
      }
    }
  }

  &-socials {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    padding-top: 40px;
  }

  &-social {
    padding: 0 8px;
  }
}

/*End Footer*/
